
import Page from 'components/Page';

import React,{useState} from 'react';
import {Carousel} from 'reactstrap';
import {
  Button,
  Card,
  CardBody,
  CardImg,
  CardText,
  CardTitle,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row
} from 'reactstrap';
const DashboardPage =(props)=> {
  var [modal,setModal]=useState(false);
  var [listNo,setListNo]=useState(0);
  var list=[{
    img:'/assets/img/bg/background_1920-11.jpg',
    title:'Card with image',
    text:`Some quick example text to build on the card title and make up
    the bulk of the card's content.`
  },{
    img:'/assets/img/bg/background_1920-11.jpg',
    title:'Card wr image',
    text:`Some quick exerple text to build on the card title and make up
    the bulk of tecontent.`
  }];
  return (

    <Page
      className="DashboardPage"    
    >
      {/* <Carousel >

      </Carousel> */}
      <Row>
        {
          list.map((ele,key)=>(
            <Col md={3} sm={6} xs={6} className="mb-3" key={key}>
              <Card>
                <CardImg top src={ele.img} />
                <CardBody>
                  <CardTitle onClick={()=>{setListNo(key);setModal(true);}} >{ele.title}</CardTitle>
                  <CardText>
                    {ele.text}
                  </CardText>
                </CardBody>
              </Card>
            </Col>
          ))
        }    
      
      </Row>
      <Modal
        isOpen={modal}
        toggle={()=>setModal(!modal)}
        className={props.className}>
        <ModalHeader toggle={()=>setModal(!modal)}>{list[listNo].title}</ModalHeader>
        <ModalBody>
          <Row>     
            <img src={list[listNo].img} ></img>  
            {list[listNo].text}                    
          </Row>
          
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={()=>setModal(!modal)}>
            OK
          </Button>{' '}
          <Button color="secondary" onClick={()=>setModal(!modal)}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
      
      
    </Page>
  );
 
}
export default DashboardPage;
